header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap:1.2rem;
    justify-content: center;

}

/* Header Socials */

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.2rem;
    position: absolute;
    left:0;
}

.header_socials::after{
    content: '';
    width: 3px;
    height: 5rem;
    background: var(--color-primary);
}

/* image */

.me{
    background: linear-gradient( var(--color-primary),transparent);
    width: 22rem;
    height: 27.3rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 3rem 1.5rem 1.5rem 1.5rem;

}

/* Scroll Dwon*/

.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}

/* MEDIA QUERIES (MEDIUM DEVICES) */

@media screen  and (max-width: 1024px){
    header{
        height: 90vh;
    }
    

}


/* MEDIA QUERIES (SMALL DEVICES) */

@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }

    .header_socials,.scroll_down{
        display: none;
    }



}