footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a{
    color: var(--color-bg);
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__links{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__links a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem ;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__links a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__links-icon{
    margin-top: 0.3rem;
    padding-right: 0.2rem;
    

}


@media screen and (max-width: 600px){
    .footer__links{
        display: grid ;
        grid-template-columns: 1fr;
        
        
    }

    .footer__links a{
        display: flex;
    justify-content: center;
    }


}